import React, { useContext, useEffect, useRef, useState } from "react";
import { Context } from "../../utils/utility";
import { styled } from "libs-player-ui-components";

const StyledTextArea = styled.textarea`
font-size: 16px;
padding: 4px 0 0 8px;
font-family: Arial;
resize: vertical;
&:focus {
    outline: none
}
`;

const StyledImageContainer = styled.div`
display: flex;
justify-content: flex-end;
padding: 4px 4px 8px 0;
`;

const StyledContainer = styled.div`
border: 1px solid darkgray;
border-radius: 7px;
background: white;
`;

const StyledImageBox = styled.div`
cursor: pointer;
&:hover {
    opacity: 0.5;
}
`;

export default function TextArea(props: any) {
    const context = useContext(Context);
    const textAreaRef = useRef<HTMLTextAreaElement>();
    const [textAreaState, setTextAreaState] = useState<string>();
    const questionTextAreaId = (props.id).split('.');
    const questionId = questionTextAreaId[0];
    const textAreaId = questionTextAreaId[1];
    const textAreaValue = context.state[questionId] && context.state[questionId][textAreaId] && context.state[questionId][textAreaId].value;

    function onInputValueChange() {
        const textAreaState = {
            [textAreaId]: {
                value: textAreaRef.current.value
            }
        };
        setTextAreaState(textAreaRef.current.value);
        context.callbacks.onStateChange(questionId, textAreaState);
    }

    function onTextAreaButtonClick() {
        context.callbacks.showModal && context.callbacks.showModal();
    }

    let textAreaStyle = {
        background: "#ffffff", 
        border: "1px solid darkgray", 
        borderRadius: "7px 7px 4px 4px",
        minHeight: "48px"
    }

    return (
        <StyledContainer>
            <StyledTextArea style={textAreaStyle} ref={textAreaRef} id={props.id} className={props.className} value={textAreaValue} onInput={onInputValueChange} />
            <StyledImageContainer>
                <StyledImageBox onClick={() => onTextAreaButtonClick()}>
                    <img src="data:image/png;base64," />
                </StyledImageBox>
                <StyledImageBox onClick={() => onTextAreaButtonClick()}>
                    <img src="data:image/png;base64," />
                </StyledImageBox>
            </StyledImageContainer>
        </StyledContainer>
    )
}