import React from 'react'
import ImageViewerComponent from '../image-viewer';
import { withTheme } from "libs-player-ui-components";


enum ElementType{
    Image = 'img',
    FigCaption = 'figcaption'
}

export default withTheme(function FigureComponent(props: any) {
    const getContent = () => {
        if(props.className.includes("image")){
            let imgComponent = props.children && props.children.find((child, index) => child.type == ElementType.Image)
            let captionComponent = props.children && props.children.find((child, index) => child.type == ElementType.FigCaption)
            let iVProps = {
                src: imgComponent && imgComponent.props.src,
                alt: imgComponent && imgComponent.props.alt,
                caption: captionComponent && captionComponent.props.children,
                captionTextColor: props.theme.palette.grey['600']
            }
            return (<div className={'max-resource-width'} ><ImageViewerComponent {...iVProps} /></div>);
        }
        else if(props.className.includes("media")){
            return (<div className={'max-resource-width media-container'}>{props.children}</div>);
        }
            return props.children;
        
    }
    return (
        <>
            {getContent()}
        </>
    )

})