import ReactDOM from "react-dom";
import React from "react";
import HTMLRendererComponent from "./components/html-renderer";

class HtmlPlayer {

    playerState;
    htmlRendererRef;
    uid;
    initEvents: any;

    constructor() {
        this.playerState = {};
        this.htmlRendererRef = React.createRef();
    }

    init(uid: string, inputHTML: string, container: HTMLElement, initOptions: any) {
        initOptions.state && (this.playerState = initOptions.state);
        this.initEvents = initOptions.events;
        this.uid = uid;
        let initContext = {
            uid: uid,
            inputHTML: inputHTML,
            callbacks: {
                onStateChange: this.stateChangeHandler.bind(this),
                launched: this.launchedEventHandler.bind(this),
                destroy: this.destroyEventHandler.bind(this),
                size: this.sizeEventHandler.bind(this),
                showModal: this.showModalHandler.bind(this)
            },
            state: this.getState()
        };
        ReactDOM.render(
            <HTMLRendererComponent ref={this.htmlRendererRef} initContext={initContext} />,
            container
        )
    }

    getState() {
        return this.playerState;
    }

    setState(state) {
        this.htmlRendererRef.current && this.htmlRendererRef.current.setState({ state: state });
    }

    protected stateChangeHandler(id, state) {
        if (this.playerState[id]) {
            this.playerState[id] = {
                ...this.playerState[id],
                ...state
            };
        } else {
            this.playerState[id] = state;
        }
        this.initEvents.change && this.initEvents.change({ uid: this.uid, type: 'change' });
    }

    protected sizeEventHandler(dimensions) {
        let sizeEventParams = {
            uid: this.uid,
            size: dimensions
        };
        this.initEvents.size && this.initEvents.size(sizeEventParams);
    }

    protected launchedEventHandler() {
        this.initEvents.launched && this.initEvents.launched({ uid: this.uid, type: 'launched' });        
    }

    protected destroyEventHandler() {
        this.initEvents.destroy && this.initEvents.destroy({ uid: this.uid, type: 'destroy' });
    }

    protected showModalHandler() {
        this.initEvents.showModal && this.initEvents.showModal({ uid: this.uid, type: 'showModal' });
    }
}

export default new HtmlPlayer();