import React, { Component } from "react";
import { ReactHTMLConverter } from "react-html-converter/node";
import MCQContainer from "../mcq";
import TextArea from "../textarera";
import FigureComponent from "../figure";
import TextBox from "../textbox";
import { Context } from "../../utils/utility";
import ReactResizeDetector from 'react-resize-detector';
import RadioButton from "../radio-button";
import ReactStickyNotes from '@react-latest-ui/react-sticky-notes';


export default class HTMLRendererComponent extends Component<any> {
    htmlConverter: any;
    ref: any;
    constructor(props) {
        super(props);
        this.htmlConverter = new ReactHTMLConverter();
        this.registerComponents();
        this.ref = React.createRef();
        this.state = {
            ...props.initContext
        }
    }

    componentDidMount() {
        const containerDOM = this.ref.current;
        this.props.initContext.callbacks.launched();
        this.props.initContext.callbacks.size({ containerFit: false, height: containerDOM.clientHeight, width: containerDOM.clientWidth });
    }

    resizeHandler() {
        const containerDOM = this.ref.current;
        this.props.initContext.callbacks.size({ containerFit: false, height: containerDOM.clientHeight, width: containerDOM.clientWidth });
    }

    componentWillUnmount() {
        this.props.initContext.callbacks.destroy();
    }

    render() {
        let stickyNotesProps = {
            sessionKey: "key1",
            notes: [
              {
                "color": "lightgreen",
                "text": "Ankush this side",
                "id": "33cbe63e-76ab-434d-ab40-f425041df5b0",
                "position": {
                  "x": 564,
                  "y": 295,
                  "px": 100,
                  "py": 100
                },
                "menu": false,
                "selected": true,
                "hidden": false,
                "datetime": "2021-02-11 12:45:49"
              }
            ],
            containerWidth: "50%",
            containerHeight: "50%",
            // noteWidth: 120,
            // noteHeight: 50,
            footer: true,
            onBeforeChange: (type, payload, notes) => {
              console.log("onBeforeChange triggered")
              console.log("type", type);  
              console.log("payload", payload);  
              console.log("notes", notes);  
              return payload;
        
            },
            onChange: (type, payload, notes) => {
              console.log("OnChange triggered")
              console.log("type", type);  
              console.log("payload", payload);  
              console.log("notes", notes);  
            }
          }
        return (
            <Context.Provider value={this.state}>
                <div ref={this.ref}>
                    <ReactStickyNotes {...stickyNotesProps} />
                    <ReactResizeDetector targetRef={this.ref} skipOnMounts={true} handleHeight onResize={this.resizeHandler.bind(this)} />
                    {this.htmlConverter.convert(this.props.initContext.inputHTML)}
                </div>
            </Context.Provider>
        )
    }

    private registerComponents() {
        this.htmlConverter.registerComponent('mcq', MCQContainer);
        this.htmlConverter.registerComponent('eng-textbox', TextBox);
        this.htmlConverter.registerComponent('eng-textarea', TextArea);
        this.htmlConverter.registerComponent('eng-radio', RadioButton);
        this.htmlConverter.registerComponent('figure', FigureComponent);
    }

}