import React, { useContext, useEffect, useRef, useState } from "react";
import { Context } from "../../utils/utility";
import { styled } from 'libs-player-ui-components';

const StyledTextBox = styled.input`
    padding-left: 8px;
    font-size: 16px;
    &:focus {
        outline: none;
    }
`;

export default function TextBox(props) {

    const inputRef = useRef<HTMLInputElement>();
    const [inputState, setInputState] = useState<string>();
    const context = useContext(Context);
    const questionTextBoxId = (props.id).split('.');
    const questionId = questionTextBoxId[0];
    const textBoxId = questionTextBoxId[1];
    const textBoxValue = context.state[questionId] && context.state[questionId][textBoxId] && context.state[questionId][textBoxId].value;


    function onInputValueChange() {
        const inputState = {
            [textBoxId]: {
                value: inputRef.current.value
            }
        };
        setInputState(inputRef.current.value);
        context.callbacks.onStateChange(questionId, inputState);
    }

    let textBoxStyle = {
        background: "#ffffff", 
        border: "1px solid darkgray", 
        borderRadius: "7px",
        minHeight: "30px"
    }

    return (
        <StyledTextBox type="text" style={textBoxStyle} ref={inputRef} className={props.className} id={props.id} value={textBoxValue} onInput={onInputValueChange} autoComplete="off"/>
    );
}