import React, { useEffect, useState } from "react";
import * as loHook from 'libs-react-xapi-runner/dist/libsReactXapiRunner.js';
import $ from 'jquery';
window['jquery'] = window['$'] = $;
function MCQContainer(props: any) {
    let lo = loHook.useLearningObject('productId');

    function hardCode() {}

    const getLORunnerInputs=()=>{
        let inputs = {
            url: "http://localhost:60259/LO-1",
            currentState: {
                fetchedState: {}
            },
            itemCode: "test Item Player",
            userId: 'userId1',
            productId: 'productId',
            classId: 'classId',
            subType: 'subType',
            setItemState: hardCode,
            postXapiStatement: hardCode,
            showResult: hardCode,
            onTerminateCallback: hardCode,
            itemStatus: 'itemStatus',
            userRole:'student',
            uiTheme:{}
        }
        return inputs;
    }

    useEffect(() => {
        lo.launchLO(getLORunnerInputs());
    }, []) 

    return (
        // Reduced width by 4px as the LO is taking width 100% and box-sizing is set to content box. And has border of 2px
        <div id={'container-lo'} style={{width: "calc(100% - 4px)"}}>
        </div>
    )
}

export default MCQContainer;