import React, { Component } from 'react';
import {ImageViewerComponent as IVComponent} from "libs-player-ui-components";
import { Context, isURL } from '../../utils/utility';

export default class ImageViewerComponent extends Component<any> {

    constructor(props) {
        super(props);
    }

    getAbsolutePath(path){
        if(!isURL(path))
            path = this.context.itemJson.mediaBaseUrl + path;
        return path;
    }
    render() {
        return (
            <IVComponent src={this.getAbsolutePath(this.props.src)} alt={this.props.alt} displayZoomButtons={this.context.displayImageZoomButtons} caption={this.props.caption} maxHeight={'80vh'} captionTextColor = {this.props.captionTextColor} zIndex={this.context.uiStyles.zIndex + 1} />
        );
    }
}

 ImageViewerComponent.contextType = Context;